.search__input {
  color:black;
  height: 50px;
  width: 80%;
  font-size: var(--font-size-homepage-items);
  border: none;
  border-radius: 0 8px 8px 0;
  padding-left: 15px;
}

.search__icon {
  display: inline-block;
  height: 50px;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px 0 0 8px;
}

::placeholder {
  color: var(--placeholder-color);
}

.search {
  display: flex;
  height: 40px;
  width: 100%;
  max-width: 360px;
  font-size: var(--font-size-homepage-items);
  box-shadow: rgba(0, 0, 0, 0.1) 1px 10px 12px;
}

.search__container {
  height: auto;
  padding: 20px 20px;
}

.options__container {
  height: 60px;
  width: 180px;
  padding-left:20px;
  border-radius: 8px;
  border: none;
  border-right-width: 16px;
  border-right-style: solid;
  border-right-color: transparent;
  font-size: var(--font-size-homepage-items);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.options {
  margin-top: 40px;
}

.grid {
  width: 100%;
  height: auto;
  padding: 0 20px;
  display: grid;
  place-items: center;
  gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
}

.card {
  height: 350px;
  width: 100%;
  max-width: 270px;
  border-radius:8px;
  overflow: hidden;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.flag {
  height: 50%;
  width: 100%;
  object-fit: fill;
  border-radius: 8px 8px 0 0;
  display: block;
}

.data {
  height: 50%;
  padding: 15px;
  border-radius: 0 0 8px 8px;
}

.name {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
}

.description {
  font-size: 16px;
}

.detailCountry {
  margin-top: 40px;
}

@media (min-width: 768px) {
  .search__container {
    display: flex;
    padding: 40px 60px;
    align-items: baseline;
    justify-content: space-between;
  }
  .options {
    margin-top: 0;
  }
  .grid {
    padding: 20px 60px;
    gap:60px
  }
}







